import React from 'react';
import './Testimonials.css';
import {Swiper, SwiperSlide} from "swiper/react";
import portfolioPic1 from '../../img/profile1.jpg';
import portfolioPic2 from '../../img/profile2.jpg';
import portfolioPic3 from '../../img/profile3.jpg';
import portfolioPic4 from '../../img/profile4.jpg';
import {Pagination} from "swiper";
import 'swiper/css/pagination';
import 'swiper/css';

const Testimonials = (props) => {
  const clients=[
    {
      img: portfolioPic1,
      review: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Ex officiis molestiae quod tempora laudantium, cumque error a nisi placeat quae exercitationem, maiores reiciendis! Eaque dicta minima, iure maiores dolorum sed.",

    },
    {
      img: portfolioPic2,
      review: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Ex officiis molestiae quod tempora laudantium, cumque error a nisi placeat quae exercitationem, maiores reiciendis! Eaque dicta minima, iure maiores dolorum sed.",
    },
    {
      img: portfolioPic3,
      review: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Ex officiis molestiae quod tempora laudantium, cumque error a nisi placeat quae exercitationem, maiores reiciendis! Eaque dicta minima, iure maiores dolorum sed.",
    },
    {
      img: portfolioPic4,
      review: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Ex officiis molestiae quod tempora laudantium, cumque error a nisi placeat quae exercitationem, maiores reiciendis! Eaque dicta minima, iure maiores dolorum sed.",
    },
  ]

  return (
    <div className="t-wrapper">
      <div className="t-heading">
        <span>Clients always get</span>
        <span>  Exceptional Work  </span>
        <span>from me...</span>

        <div className="blur t-blur1" style={{
          background: 'var(--purple)'
        }}/>
        <div className="blur t-blur2" style={{
          background: 'skyblue'
        }}/>

      </div>
      <Swiper
        modules={[Pagination]}
        slidesPerView={1}
        pagination={{clickable: true}}
      >
        {clients.map((clients, index) => {
          return(
            <SwiperSlide key={index}>
              <div className="testimonial">
                <img src={clients.img} alt=""/>
                <span>{clients.review}</span>
              </div>
            </SwiperSlide>
          )
        })}
      </Swiper>
    </div>
  );
}

export default Testimonials;